.App{
  display: grid;
  grid-template-columns:1fr 3fr;
  width:100vw;
  height:100vh;
}


button{
  cursor: pointer;
}

body::-webkit-scrollbar-thumb {
  background-color: red;
  outline: 1px solid slategrey;
}

@media only screen and (max-width:992px){
  .App{
    display: inline;
  }
}
